#marquee {
  position: relative;
  height: 140px;
  background: #e75e26;
  white-space: nowrap;
  overflow: hidden;
  -webkit-transition: background-color .3s;
  -o-transition: background-color .3s;
  transition: background-color .3s;
  cursor: pointer;
}

.marquee-text {
  position: absolute;
  top: 50%;
  left: 100vw;
  font-family: 'Oswald';
  font-size: 90px;
  letter-spacing: -4px;
  color: #111111;
  text-transform: uppercase;
  margin: 0;
  -webkit-transform: translateY(-50%);
      -ms-transform: translateY(-50%);
          transform: translateY(-50%);
  -webkit-transition: color .7s;
  -o-transition: color .7s;
  transition: color .7s;
}

#marquee:hover {
  background: #111111;
}

#marquee:hover .marquee-text {
  color: #e75e26;
}



