#carousel-container {
  position: relative;
  overflow: hidden;
  background: #111111;
  cursor: pointer;
}

#image-container {
  position: relative;
  height: calc(100vw / (16 / 9));
  background-position: center;
  background-size: cover;
  opacity: 1;
  -webkit-transition: opacity 3s;
  -o-transition: opacity 3s;
  transition: opacity 3s;
}

/* Position Buttons */
#circle-container {
  display: -ms-flexbox;
  display: flex;
  position: absolute;
  left: 50%;
  bottom: 50px;
  -webkit-transform: translateX(-50%);
      -ms-transform: translateX(-50%);
          transform: translateX(-50%);
  width: 60px;
  height: 20px;
}

.circle-svg {
  width: 20px;
  height: 20px;
  cursor: pointer;
}

.circle-svg > circle {
  width: 10px;
  height: 10px;
  cx: 10;
  cy: 10;
  r: 5;
  -webkit-transition: fill 1s;
  -o-transition: fill 1s;
  transition: fill 1s;
}



