.typelogo-nav {
  float: left;
  fill:#ededed;
  stroke:#ededed;
  stroke-width: 0.25;
  stroke-miterlimit: 1;
  margin-top: 15px;
  margin-left: 60px;
  -webkit-transition: fill 0.3s, stroke 0.3s;
  -o-transition: fill 0.3s, stroke 0.3s;
  transition: fill 0.3s, stroke 0.3s;
  width: 180px;
}

.typelogo-footer {
  display: block;
  float: left;
  fill:#ededed;
  stroke:#ededed;
  stroke-width: 0.25;
  stroke-miterlimit: 1;
  margin-bottom: 40px;
  -webkit-transition: fill 0.3s, stroke 0.3s;
  -o-transition: fill 0.3s, stroke 0.3s;
  transition: fill 0.3s, stroke 0.3s;
  width: 180px;
}

.typelogo-about {
  display: block;
  position: relative;
  fill: #ededed;
  stroke: #ededed;
  stroke-width: 0.25;
  stroke-miterlimit: 1;
  width: 500px;
  margin: 40px auto;
}

@media (max-width: 700px) {
  .typelogo-about {
    width: 300px;
  }
}

.typelogo-white {
  fill: #ededed;
  stroke: #ededed;
  stroke-width: 0.25;
  stroke-miterlimit: 1;
}



