.buy-button {
  height: 30px;
  background: #e75e26;
  border: none;
  margin: 0;
  font-family: 'Rajdhani';
  font-size: 18px;
  font-weight: 600;
  text-transform: uppercase;
  -webkit-transition: background-color .3s, color .7s;
  -o-transition: background-color .3s, color .7s;
  transition: background-color .3s, color .7s;
}

.buy-button-module {
  position: absolute;
  bottom: 0;
  right: 0;
  width: 100%;
  -ms-flex-positive: 1;
      flex-grow: 1;
}

.buy-button:hover {
  background: #111111;
  color: #e75e26;
}



